/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';

import ImgBgSrc from "url:./bg.jpg";
import ImgBg from "../../components/ImgBg";
import TextWin, {TextWinFoot, TextWinTextArea, TextWinTitle} from "../../components/TextWin";
import ButtonNext from "../../components/ButtonNext";
import Stage from "../../components/Stage";
import {useEffect, useState} from "preact/hooks";
import LinkDelay from "../../components/LinkDelay";

export default function Page7() {
  const [showText, setShowText] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);

  useEffect(() => {
    setShowText(true);

    // 当前页面必须播放的时间
    setTimeout(() => setCanGoNext(true), 600);
  }, []);
  return (
    <Stage>
      <ImgBg src={ImgBgSrc}/>
      <TextWin show={showText}>
        <TextWinTextArea>
          <TextWinTitle className="mb4">
            &lt;QUEST 2: FIGHT AGAINST KYLE&gt;
          </TextWinTitle>
          What a good chance! He searched for Kyle on campus and finally found him on the playground.
        </TextWinTextArea>
        <TextWinFoot>
          <ButtonNext/>
        </TextWinFoot>
      </TextWin>
      <LinkDelay
        title="next"
        href="/page8"
        disabled={!canGoNext}
      />
    </Stage>
  );
};
