/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';

import ImgBgSrc from "url:./bg.jpg";
import ImgBg from "../../components/ImgBg";
import TextWin, {TextWinFoot, TextWinTextArea} from "../../components/TextWin";
import ButtonNext from "../../components/ButtonNext";
import Stage from "../../components/Stage";
import StageMask from "../../components/StageMask";
import LinkDelay from "../../components/LinkDelay";
import {useEffect, useState} from "preact/hooks";

function Page2() {
  const [showText, setShowText] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);
  const [showMask, setShowMask] = useState(true);

  useEffect(() => {
    setShowMask(false);

    // 显示文案
    setTimeout(() => setShowText(true), 200);

    // 当前页面必须播放的时间
    setTimeout(() => setCanGoNext(true), 700);
  }, []);

  return (
    <Stage>
      <ImgBg src={ImgBgSrc}/>
      <StageMask out={!showMask}/>
      <TextWin show={showText}>
        <TextWinTextArea>
          Quinn was a weak boy. Since his parents had died in the war, he was often bullied by Kyle at school. He hated
          Kyle.
        </TextWinTextArea>
        <TextWinFoot>
          <ButtonNext/>
        </TextWinFoot>
      </TextWin>
      <LinkDelay
        title="next"
        href="/page3"
        disabled={!canGoNext}
      />
    </Stage>
  );
}

export default Page2;
