/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';
import {route} from 'preact-router';
import ROUTER_PREFIX from "../ROUTER_PREFIX";

function LinkDelay({
                     className = "", disabled = false, href, delay = 0, onClick = () => {
  }, ...props
                   }) {
  const handleClick = (e) => {
    e.preventDefault();
    onClick(e);
    setTimeout(() => {
      route(`${ROUTER_PREFIX}${href}`);
    }, delay);
  };
  if (disabled) {
    return null;
  }
  return (
    <a className={`${className} pa t0 l0 w100% h100% zi1`} onClick={handleClick} {...props}/>
  );
}

export default LinkDelay;
