/** @jsxFrag Fragment */

import {h} from 'preact';

import ImgBgSrc from "url:./bg.jpg";
import ImgBg from "../../components/ImgBg";
import TextWin, {TextWinFoot, TextWinTextArea} from "../../components/TextWin";
import ButtonNext from "../../components/ButtonNext";
import Stage from "../../components/Stage";
import StageMask from "../../components/StageMask";
import {useEffect, useState} from "preact/hooks";
import LinkDelay from "../../components/LinkDelay";

function Page5() {
  const [showText, setShowText] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);

  useEffect(() => {
    setShowText(true);

    // 当前页面必须播放的时间
    setTimeout(() => setCanGoNext(true), 600);
  }, []);
  return (
    <Stage>
      <ImgBg src={ImgBgSrc}/>
      <TextWin show={showText} red>
        <TextWinTextArea className="ttu">
          &lt;CONGRATULATIONS! YOU ARE GRANTED VAMPIRE SYSTEM. You need to complete the quests to level up.&gt;
        </TextWinTextArea>
        <TextWinFoot>
          <ButtonNext/>
        </TextWinFoot>
      </TextWin>
      <LinkDelay
        title="next"
        href="/page6"
        disabled={!canGoNext}
      />
    </Stage>
  );
}

export default Page5;
