/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';

import {useEffect, useState} from "preact/hooks";
import * as style from './style.module.less';
import * as styleflash from "../../components/Button/flash.module.less";
import ImgBgSrc from "url:./bg.jpg";
import ImgBg from "../../components/ImgBg";
import Button from "../../components/Button";
import Stage from "../../components/Stage";
import LinkDelay from "../../components/LinkDelay";
import StageMask from "../../components/StageMask";

function Page1() {
  const [runBg, setRunBg] = useState(false);
  const [showIn, setShowIn] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);
  const [showMask, setShowMask] = useState(false);

  useEffect(() => {
    setRunBg(true);
    // 背景显示时间
    setTimeout(() => setShowIn(true), 300);
    // 当前页面必须播放的时间
    setTimeout(() => setCanGoNext(true), 1000);
  }, []);

  return (
    <Stage>
      <ImgBg className={`${style.bg} td300 pen ${runBg ? '_in' : ''}`} src={ImgBgSrc}/>

      <div className={`pa l0 w100% t50% tac ttY-50% ${style.main} ${showIn ? '_trans_in' : ''}`}>
        <h2 className="c_fff fs24 lh1.2 fw700 mb32 _trans_up td300">
          <span className="db">WILL YOU FIGHT</span>
          <span className="db">TO AWAKE YOUR</span>
          <span className="db">HIDDEN ABILITY?</span>
        </h2>
        <Button Component="strong"
                className={`${style.btn} _block mla mra _trans_up td300 tde200 ${styleflash.flash}`}>
          AWAKE NOW
        </Button>
      </div>
      <StageMask show={showMask}/>
      <LinkDelay
        title="next"
        href="/page2"
        delay={500}
        disabled={!canGoNext}
        onClick={() => {
          setShowIn(false);
          setShowMask(true);
        }}
      />
    </Stage>
  );
};

export default Page1;
