/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';

import * as style from "./style.module.less";
import TextWin, {TextWinTextArea} from "../../components/TextWin";
import Stage from "../../components/Stage";
import {useEffect, useState} from "preact/hooks";
import ImgBook from "url:./book.png";
import ImgBlood from "url:./blood.svg";
import ImgHand from "url:./hand.png";
import TriggerButton from "../../components/TriggerButton";
import ClickHand from "../Page6/ClickHand";
import {route} from 'preact-router';

function Page3() {
  const [showBook, setShowBook] = useState(false);
  const [showHand, setShowHand] = useState(false);
  const [showBtn, setShowBtn] = useState(false);
  const [showBloodScale, setShowBloodScale] = useState(false);
  const [showBloodDrop, setShowBloodDrop] = useState(false);
  const [bookShack, setBookShack] = useState(false);
  const [showText, setShowText] = useState(false);
  const [canGoOn, setCanGoOn] = useState(false);

  useEffect(() => {
    setShowBook(true);
    setTimeout(() => setShowText(true), 300);
    setTimeout(() => setShowBtn(true), 500);
    setTimeout(() => setCanGoOn(true), 1000);
  }, []);


  const handleGoOn = (e) => {
    e.preventDefault();
    if (canGoOn) {
      setCanGoOn(false);
      setShowBtn(false);
      setShowHand(true)
      setTimeout(() => setShowBloodScale(true), 400);
      setTimeout(() => setShowBloodDrop(true), 700);
      setTimeout(() => setBookShack(true), 1300);
      setTimeout(() => {
        route('/page5');
      }, 1700);
    }
  };
  return (
    <Stage className={`${style.stage}`}>
      <img className={`${style.hand} ${showHand ? '' : '_hide'} pa t0 l0 w100% td300 pen`} src={ImgHand}/>
      <div className={`${style.blood_box} ${showBloodDrop ? '_move' : ''} pa pen`}>
        <img width="40" height="54" className={`${style.blood} td200 ${showBloodScale ? '' : '_hide'}`} src={ImgBlood}/>
      </div>
      <div className={`${style.book_wrap} pa l0 b0 w100% ${showBook ? '' : '_hide'}`}>
        <img className={`${style.book} pa l0 w100% ha ${bookShack ? '_shack' : ''} `} src={ImgBook} alt="book"/>
        {showBtn ? <TriggerButton className={`${style.btn} pa l50% cp`}>
          <div className={`${style.btn_hand} pa`}>
            <ClickHand className={`pa t100% l0 td300`}/>
          </div>
        </TriggerButton> : null}
      </div>
      <TextWin show={showText}>
        <TextWinTextArea>
          When he touched the book, his glasses suddenly broke and the shard cut his thumb. His blood dropped on the
          bookcover.
        </TextWinTextArea>
      </TextWin>
      {canGoOn ? <a className="pa t0 l0 w100% h100% zi1" onClick={handleGoOn} title="Click"/> : null}
    </Stage>
  );
}

export default Page3;
