/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';

import ImgBgSrc from "url:./bg.jpg";
import ImgBg from "../../components/ImgBg";
import TextWin, {TextWinFoot, TextWinTextArea} from "../../components/TextWin";
import ButtonNext from "../../components/ButtonNext";
import Stage from "../../components/Stage";
import {useEffect, useState} from "preact/hooks";
import LinkDelay from "../../components/LinkDelay";

function Page3() {
  const [showText, setShowText] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);

  useEffect(() => {
    setShowText(true);
    // 当前页面必须播放的时间
    setTimeout(() => setCanGoNext(true), 700);
  }, []);
  return (
    <Stage>
      <ImgBg src={ImgBgSrc}/>
      <TextWin show={showText}>
        <TextWinTextArea>
          One day, Quinn found an old book left by his parents.
        </TextWinTextArea>
        <TextWinFoot>
          <ButtonNext/>
        </TextWinFoot>
      </TextWin>
      <LinkDelay
        title="next"
        href="/page4"
        disabled={!canGoNext}
      />
    </Stage>
  );
}

export default Page3;
