import {h} from 'preact';

import * as style from './style.module.less';
import RedMask from "./RedMask";

function TextWin({className = "", show = false, red = false, children = null, ...props}) {
  return (
    <div className={`${style.wrap} ${show ? '_show' : ''} ${red ? "_red" : ''} pa l0 b0 w100% mb24 td300`}>
      <div className={`${className} ${style.box} pr ml16 mr16 bc_fff c_000 pt8 pb8 pl8 pr8 br2`} {...props}>
        {red ? (<RedMask/>) : null}
        {children}
      </div>
    </div>
  );
}

function TextWinTextArea({className = "", ...props}) {
  return (
    <div className={`${className} ${style.text} mb4 pt12 pb12 pl16 pr8 br8 fs16 lh1.2 td300 tde200`} {...props} />
  )
};

function TextWinTitle({className = "", ...props}) {
  return (
    <h2 className={`${className} c_danger fs14 lh1.2 fw700 ttu`} {...props} />
  )
};

function TextWinFoot({className = "", ...props}) {
  return (
    <div className={`${className} ${style.foot} tar td300 tde500`} {...props} />
  )
};

export default TextWin;
export {TextWinTextArea, TextWinFoot, TextWinTitle};
