/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';
import {useEffect, useState} from "preact/hooks";
import ImgBg from "../../components/ImgBg";
import TextWin, {TextWinFoot, TextWinTextArea} from "../../components/TextWin";
import ButtonNext from "../../components/ButtonNext";
import Stage from "../../components/Stage";
import LinkDelay from "../../components/LinkDelay";
import TriggerButton from "../../components/TriggerButton";
import * as style from './style.module.less';
import ImgBgSrc from "url:./bg.jpg";
import ImgBg2 from "url:./2.jpg";
import ClickHit from "./ClickHit";

export default function Page8() {
  const [showText, setShowText] = useState(false);
  const [showBg2, setShowBg2] = useState(false);
  const [showText2, setShowText2] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);
  const [showHit, setShowHit] = useState(false);
  const [showHitMove, setShowHitMove] = useState(false);

  useEffect(() => {
    setShowHit(true);
    // 显示文案
    setTimeout(() => setShowText(true), 200);

  }, []);

  const goNext = (e) => {
    e.preventDefault();
    if (!canGoNext) {
      setShowHitMove(true);
      setTimeout(() => {
        setShowText2(true);
        setShowBg2(true);
        setShowText(false);
      }, 700);
      setTimeout(() => setShowHit(false), 800);
      setTimeout(() => setCanGoNext(true), 1600);
    }
  };
  return (
    <Stage>
      <ImgBg src={ImgBgSrc}>
        <img className={`pa l0 t0 w100% ha pen ${showBg2 ? '' : 'o0 vh'} td300`} src={ImgBg2} alt="&nbsp;"/>
      </ImgBg>
      <TriggerButton size={82}
                     className={`pa tde200 ${style.btn} ${showHit ? '' : 'o0 vh'}`}>
        <div className="pa t50% l50% tt-50%">
          <ClickHit shack={!showHitMove} className={`${style.clickHit} ${showHitMove ? '_hit_it' : ''}`}>
            {!showHitMove ? (<span>Hit <br/> here</span>) : null}
          </ClickHit>
        </div>
      </TriggerButton>
      <TextWin show={showText}>
        <TextWinTextArea>
          Quinn threw out a kick on Kyle's stomach, causing him to stumble on the ground, and then quickly punched him
          in the face with full force.
        </TextWinTextArea>
      </TextWin>
      <TextWin show={showText2}>
        <TextWinTextArea>
          Kyle was knocked down.
        </TextWinTextArea>
        <TextWinFoot>
          <ButtonNext/>
        </TextWinFoot>
      </TextWin>
      <LinkDelay
        title="next"
        href="/page9"
        disabled={!canGoNext}
      />
      {!canGoNext ? <a className="pa t0 l0 w100% h100% zi1" onclick={goNext} title="hit here"/> : null}
    </Stage>
  );
};
