import {h} from 'preact';

import * as style from './style.module.less';
import ImgEye from "url:./eye.png";

function Process({className = "", show = false, process = 1, red = false, children = null, ...props}) {
  return (
    <div className={`${style.wrap} ${className} br2 ${show?'':'_hide'} td300`} {...props}>
      <span className={`${style.number} pa c_danger fw700 fs16`}>{process}/3</span>
      {children}
      <div className={`${style.box} pa t0 l0 h100% br2 td300`} style={{width: `${parseInt(process / 3 * 100)}%`}}>
        <img className="pa t50%" src={ImgEye} alt="&nbsp;"/>
      </div>
    </div>
  );
}


export default Process;
