/** @jsx h */
/** @jsxFrag Fragment */

import {h} from 'preact';
import * as style from './style.module.less';
import ImgBgSrc from "url:./bg.jpg";
import ImgBg from "../../components/ImgBg";
import TextWin, {TextWinFoot, TextWinTextArea, TextWinTitle} from "../../components/TextWin";
import Stage from "../../components/Stage";
import {useEffect, useState} from "preact/hooks";
import ImgBg2 from "url:./2.jpg";
import ImgBg3 from "url:./3.jpg";
import Process from "./Process";
import TriggerButton from "../../components/TriggerButton";
import ClickHand from "./ClickHand";
import ButtonNext from "../../components/ButtonNext";
import LinkDelay from "../../components/LinkDelay";
import Exp from "./Exp";


export default function Page6() {
  const [process, setProcess] = useState(0);
  const [showText, setShowText] = useState(false);
  const [showText2, setShowText2] = useState(false);
  const [canGoNext, setCanGoNext] = useState(false);
  const [showProcess, setShowProcess] = useState(true);
  const [showExp, setShowExp] = useState(false);

  useEffect(() => {
    setShowText(true);
  }, []);

  const upProcess = (e) => {
    e.preventDefault();
    if (process < 3) {
      setProcess(process + 1);
    }
    if (process === 2) {
      setTimeout(() => {
        setShowProcess(false);
        setShowText(false);
        setShowText2(true);
        setShowExp(true);
        setTimeout(() => setCanGoNext(true), 600);
      }, 400);
    }
  };
  return (
    <Stage>
      <ImgBg src={ImgBgSrc}>
        <img className={`pa l0 t0 w100% ha td300 pen ${process === 2 ? '' : 'o0 vh'}`} src={ImgBg2} alt="&nbsp;"/>
        <img className={`pa l0 t0 w100% ha td300 pen ${process > 2 ? '' : 'o0 vh'}`} src={ImgBg3} alt="&nbsp;"/>
      </ImgBg>
      <div className={`${style.btn_box} pa t0 l0 w100%`}>
        <TriggerButton className={`pa cp ${style.btn} ${process < 3 ? '' : 'o0 pen vh'} ${showProcess ? '' : 'o0 vh'}`}>
          <ClickHand className={`pa ${style.hand} t100% l0 td300`}>
            click to <br/>
            drink water
          </ClickHand>
        </TriggerButton>
      </div>
      <Process process={process} show={showProcess} className="pa l50% t0"/>
      <Exp className={`pa t0 l50% ${style.exp} ${showExp ? '' : '_hide'} td300`}/>
      <TextWin show={showText}>
        <div className="mb32"/>
        <TextWinTextArea>
          <TextWinTitle>
            &lt;QUEST 1: DRINK TWO LITERS OF WATER&gt;
          </TextWinTitle>
        </TextWinTextArea>
        <div className="mb32"/>
      </TextWin>
      <TextWin show={showText2}>
        <TextWinTextArea>
          Quinn felt the sensation of power and anger rushing through his body. He had never such a strong desire for
          revenge to Kyle.
        </TextWinTextArea>
        <TextWinFoot>
          <ButtonNext/>
        </TextWinFoot>
      </TextWin>
      <LinkDelay
        title="next"
        href="/page7"
        disabled={!canGoNext}
      />
      {!canGoNext ? <a className="pa t0 l0 w100% h100% zi1" onclick={upProcess} title="hit here"/> : null}
    </Stage>
  );
};
